.bg-DshBrd{
    position:fixed;
   background-image: url('https://source.unsplash.com/FewHpO4VC9Y/1600');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
    top:0;
   height: 100%;
   width: 100%;
    overflow: scroll;
    z-index: -1;
}

.bg-DshBrd::-webkit-scrollbar {
    display: none;
  }
 
  .wrap-proj-chat{
      display: flex;
  }

.proj-desc{
    position: absolute;
    top:4rem;
    width: 90%;
    display: flex;
    flex-direction: column;
    margin: 0.1rem;
    justify-content: center;
    padding: 2.0em;
    left: 1rem;
    right:1rem;
    /* position: relative;
    top:4.0rem; */
    align-items: center;
}

.proj-info>h1{
    font-size: 3rem;
    color: rgb(0, 16, 26);
}

.proj-info{
    width: 90%;
    background-color: #e8f7ff7c;
    padding: 2.0em;
    border-radius: 5px;
    position: relative;
    left: 1rem;
    right:1rem;
    margin:0.5rem;
    color: rgb(0, 16, 26);
}

